var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("莱娅公主")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        奥德兰公主莱娅（也参议员莱娅奥加纳或一般莱娅奥加纳）是一个虚构人物星球大战专营权，由电影刻画嘉莉费雪。莱娅于1977年在原版“星球大战”电影中首次亮相，是奥尔德兰星球的公主，是帝国参议院的成员，也是反叛联盟的代理人。她阻止了邪恶的西斯勋爵 达斯维达，并帮助摧毁了帝国的灾难性超级武器 - 死星。在帝国反击战中（1980年），莱娅指挥一个反叛者基地并躲避维德，因为她爱上了走私者汉索罗。在“绝地归来”（1983年）中，莱娅领导的行动是从哈特贾巴的犯罪领主中 拯救汉族，并被揭露为维德的女儿和卢克天行者的双胞胎妹妹。的前传电影西斯的复仇（2005）规定，这对双胞胎的母亲是参议员（和前女王）帕德美·阿米达拉的纳布，分娩后谁死。Leia被Alderaan的参议员Bail和女王Breha Organa收养。在The Force Awakens（2015）和最后绝地（2017年），莱娅是抵抗第一秩序的创始人和将军。她和韩有一个名为奔儿子，谁通过了名 Kylo仁转向黑暗面后的部队。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        在星战传奇系列小说，漫画和视频游戏（1977年至2014年），这是在另外一组的连续性，累啊继续她与韩和卢克冒险之后的绝地归来，战斗帝国resurgences和新威胁的星系。她成为国家元首的的新共和国和绝地大师，并且是由汉族母亲三个孩子：吉安娜，杰森打倒和阿纳金独奏。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        莱娅是由星球大战的创造者乔治卢卡斯创建的，他在1999年解释了他早期发展的主角：第一个版本谈到了一位公主和一位老将军。第二版涉及父亲，儿子和女儿; 女儿是这部电影的女主角。现在女儿已成为卢克，马克哈米尔的角色。还有两个兄弟的故事，我把其中一个变成了姐姐。这位哥哥被监禁了，这位年轻的妹妹不得不拯救他并把他带回他们的父亲那里。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        在“ 星球大战”的草稿中，莱娅是国王卡约斯和Aquilae女王布雷哈的被宠坏的十几岁女儿，有两个兄弟，比格斯和温迪; 比格斯作为卢克的童年朋友回到了第四次选秀。莱娅有一次“ 欧文拉尔斯的女儿和他的妻子贝鲁，卢克的堂兄，他们一起参观了他母亲的坟墓，他的父亲在死星摧毁的星球上丧生。”后来的故事概要将莱娅定为“莱娅安的列斯群岛”，这是来自安纳瓦少校和平世界的保罗安的列斯群岛的女儿。在第四稿中确定了“Leia Organa”")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","heig